html, body {
  height: auto;
  height: initial
}
.App {
  max-width: 1200px;
  margin: 0 auto;
  font-family: 'Roboto', sans-serif;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  background-color: #2dffff;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.3);
}
.content {
  display: flex;
  justify-content: space-between;
}
@media (max-width:600px) {
  .content {
  flex-wrap: wrap;
  }
}
.card {
  height: 215px;
  overflow: hidden;
  text-align: center;
  border-radius: 3px;
  box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.3);
  background-color: rgba(0, 0, 0, 0.10);
  transition: linear 0.2s;
}
.card:hover {
  background-color: rgba(0, 0, 0, 0.15);
}
.card a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.75);
}
.card-head{
  height: 116px;
}
.card-head img {
  height: 60px;
  width: 60px;
  margin: 10px 0 5px 0;
  border-radius: 50%;
  background-color: white;
}
.card-head h2 {
  height: 21px;
  margin: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 20px;
  font-weight: bold;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.75);
}
.card-head h3 {
  height: 16px;
  margin: 0 0 4px 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 15px;
  line-height: 15px;
  color: rgba(0, 0, 0, 0.75);
}
.card-description {
  height: 64px;
  padding: 0 10px;
  line-height: 64px;
  text-align: center;
}
.card-description p {
  display: inline-block;
  vertical-align: middle;
  max-height: 48px;
  margin: 0;
  overflow: auto;
  line-height: 16px;
  word-break: break-word;
}
.card-minor {
  display: flex;
  height: 35px;
  font-weight: bold;
}
.icon {
  position: relative;
  bottom: 1px;
}
.lang-circle {
  position: relative;
  bottom: 1px;
  flex-shrink: 0;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: white;
}
.card-minor-info {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 0;
}
.card-minor-info span {
  margin-left: 3px;
  line-height: 12px;
}

.repo-list  {
  width: 100%;
}
.item-list {
  display: grid;
  grid-template-columns: 32.7% 32.7% 32.7%;
  grid-column-gap: 0.95%;
  grid-row-gap: 10px;
  margin-left: 10px;
  padding-top: 10px;
}
.full-app-message {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
}
.low-app-message {
  width: 100%;
  padding: 50px 0;
  text-align: center;
}
.message {
  position: absolute;
  margin: 0;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
} 
@media(min-width:900px) and (max-width:1200px) {
  .item-list {
    grid-template-columns: 32.7% 32.7% 32.7%;
    grid-column-gap: 0.95%;
  }
}
@media (min-width:600px) and (max-width:900px) {
  .item-list {
    grid-template-columns: 49.5% 49.5%;
    grid-column-gap: 1%;
  }
}
@media (max-width:600px) {
  .item-list {
    grid-template-columns: 100%;
    grid-column-gap: 0;
  }
}
.filter-bar {
  width: 200px;
  flex-shrink: 0;
  margin-top: 10px;
  padding: 15px 20px 10px 20px;
  border-radius: 5px;
  box-shadow: 1px 0 2px 0 rgba(0, 0, 0, 0.3);
  background-color: rgba(0, 0, 0, 0.10);
}
.filter-bar label {
  font-size: 18px;
}
.filter-antd-option {
  display: block;
  margin-bottom: 6px;
  line-height: 32px;
}
.filter-option {
    -webkit-font-feature-settings: "tnum";
            font-feature-settings: "tnum";
    font-variant: tabular-nums;
    font-size: 14px;
    box-sizing: border-box;
    height: 32px;
    padding: 4px 11px;
    margin-bottom: 6px;
    color: rgba(0, 0, 0, 0.65);
    background-color: #fff;
    transition: all 0.3s;
    display: block;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    width: 100%;
}
.filter-option:hover {
  border-color: #40a9ff;
  border-right-width: 1px ;
}

.filter-option:focus {
  border-color: #40a9ff;
  outline: 0;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
  border-right-width: 1px;
}

.filter-option::-webkit-input-placeholder {
  opacity: 0.5;
}

.filter-option::-ms-input-placeholder {
  opacity: 0.5;
}

.filter-option::placeholder {
  opacity: 0.5;
}

.filter-option::-webkit-inner-spin-button, 
.filter-option::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.filter-option-checkbox {
  margin-left: 10px;
}

.filter-option-small {
  width: 95px;
}

@media (max-width:600px) {
  .filter-bar {
    height: 450px;
    width: 100%;
  }
  .filter-option-small {
    width: 100%;
  }
}
.repo-search {
  width: 100%;
  padding: 10px 20px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  background-color: rgba(90, 166, 176, 0.45);
}
.search-result {
  text-align: center;
  margin: 0 0 -10px 0;
  font-size: 18px;
  font-weight: 400;
}
*, *:before, *:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #b5ffff;
}

